.Roller__Box__Bet {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;

  font-family: Unlock, "Gilroy", Arial, serif;
  transform: translateX(-1px);
}

.rollerClass.rollerClass {
  margin-top: 6%;
  font-weight: 400;
  font-size: 33.2444px;
  line-height: 36px;
  color: #00ffc3;
  border: none !important;

  transition: color 1s;
  position: relative;
  & > div {
    align-items: center;
  }
}

.rollerClass::after,
.rollerClass::before {
  position: absolute;
  content: "";
  display: block;
  width: 100%;
  left: 0;
  z-index: 3;
}

.rollerClass::after {
  top: -10px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.5) 0%, transparent);

  height: 60px;
}

.rollerClass::before {
  bottom: -10px;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.5) 10%, transparent);
  height: 90px;
}

.DigitRoll__Cell {
  margin: 0;
  position: relative;

  z-index: 0;
  filter: contrast(1.2);
}

.DigitRoll__Divider {
  display: none !important;
}

.DigitRoll__Divider {
  font-size: 90px;
  color: transparent;
  padding: 0;
}

.betAutoOn.betAutoOn {
  transition: color 0.4s;
}

.betTextFail.betTextFail {
  color: #dd5389;
}

.Roller__afterNumber_bet {
  position: absolute;
  top: 2.9rem;
}
.Roller__beforeNumber_bet {
  position: absolute;
  top: -1.5rem;
}
