@import "sass/index";
@import "../../variables.module.scss";
$desktop-gap: 10px;
$mobile-gap: 5px;
.root {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 301px;
  border-radius: 4.88889px;
  box-shadow: inset 0px 0.488889px 0.977778px rgba(255, 255, 255, 0.107135);
  background: $dice;
  @include backgroundOptions;
}
.dataWrapper {
  width: 250px;
  height: 100px;
  transform-origin: left top;
  position: relative;
  top: -13%;
}
.imgPanel {
  object-fit: cover;
  width: 100%;
  transform: scale(1.4);
}
