$default: url("/assets/logos/betinfo_background-pasino-logo.svg"),
  radial-gradient(
    50% 50% at 50% 50%,
    rgba(25, 29, 58, 0) 29.69%,
    rgba(0, 0, 0, 0.1) 100%
  );
$crash: url("/assets/crash/bg.svg"), rgba(20, 21, 54, 0.3);
$dice: url("/assets/logos/betinfo_background-pasino-logo.svg"),
  rgba(20, 21, 54, 0.5);
$limbo: url(/assets/limbo/img/betSpace.svg);

@mixin backgroundOptions {
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  overflow: hidden;
}
